.header {
  position: sticky;
  top: 0;
  z-index: 3;
}
.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 70px;
  box-shadow: 0 3px 6px #00000029;
  background: white;
}
.header__nav {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}
.header__nav-item {
  text-align: center;
  font-weight: bold;
  font-size: 11px;
  font-family: "Open Sans", sans-serif;
  letter-spacing: 3px;
  text-transform: uppercase;
  margin-left: 66px;
  color: #A5A5A5;
  padding: 0 12px;
  height: calc(100% - 3px);
  display: flex;
  align-items: center;
  border-bottom: 3px solid rgba(0, 0, 0, 0);
}
.header__nav-item:hover {
  border-bottom: 3px solid #08A6E4;
  box-sizing: initial;
}
.header__nav-item:hover a {
  color: black;
}
.header__nav-item a {
  padding: 29px 0;
  color: #A5A5A5;
}

.menu {
  height: 100%;
}

.hamburger {
  display: none;
}

@media (max-width: 850px) {
  .header__nav-item {
    margin-left: 3vw;
  }
}
@media (max-width: 660px) {
  .hamburger {
    display: initial;
    transform: scale(1.5);
  }

  .menu {
    display: block;
    position: absolute;
    top: -123px;
    right: 0;
    width: 100%;
    z-index: -1;
    transition: transform 0.7s ease-in-out;
  }

  .header__nav {
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    box-shadow: 0 3px 6px #00000029;
    background: white;
    height: 175px;
    width: 100%;
  }
  .header__nav-item {
    height: 30px;
    padding: 0;
    margin: 0 15px;
  }
  .header__nav-item:hover {
    border-bottom: initial;
  }
  .header__nav-item a {
    height: 100%;
    padding: 5px 0 0 0;
    line-height: 19px;
  }

  .show {
    transform: translate(0, 193px);
  }
}
@media (max-width: 420px) {
  .hamburger {
    transform: none;
  }
}
